        text: "Всё, что нужно для вашего комфортного участия, мы уже собрали в один готовый пакет:",
        list: [
          {
            title: "Отели",
            text: "Все детали заселения и проживания заранее продуманы и согласованы.",
          },
          {
            title: "Питание",
            text: "Завтраки и ужины уже включены в пакет.",
          },
          {
            title: "Трансферы",
